html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

* {
  box-sizing: border-box;
}

body, li, ul, nav, h2, p {
  margin: 0;
}

.header {
  text-align: center;
  justify-content: space-between;
  width: auto;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin: auto 0;
}

.nav__list {
  text-align: center;
  margin: 0;
  padding-inline-start: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-align: center;
  margin-left: 20px;
  display: flex;
}

.nav__list .nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  text-align: center;
  align-items: center;
  width: auto;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.stars {
  justify-content: space-between;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: block;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog__card {
  padding-inline: 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding-top: 32px;
  padding-bottom: 16px;
  transition: color, transform .3s;
  display: flex;
}

.catalog__card-img {
  flex-shrink: 0;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.catalog__card-title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.catalog__card-product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.catalog__card-stars-and-reviews {
  justify-content: space-between;
  margin: 0 0 24px;
  display: flex;
}

.catalog__card-reviews {
  color: #060b35;
  justify-content: end;
  align-items: flex-end;
  height: 16px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.catalog__card-price {
  justify-content: space-between;
  margin: 0 0 16px;
  display: flex;
}

.catalog__card-price-name {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.catalog__card-price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.catalog__card:hover {
  transform: scale(1.2);
}

.catalog__card:hover .catalog__card-title {
  color: #34568b;
}

.catalog__card-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: border, color, background-color .3s;
  display: flex;
}

.catalog__card-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

